<script>
import axios from "axios"
export default {
  name: "PageFactura",
  data() {
    return {
      datosLote: "",
      idLote: ""
    };
  },
  methods: {
    lote() {
      this.datosLote = JSON.parse(localStorage.getItem("datosLote"))
      this.idLote = this.datosLote._id
      console.log(this.datosLote);
    },

    listarSaleLotCattle() {
      let header = { headers: { "x-token": localStorage.getItem("token") } };
      axios.get(`lotcattle/${this.idLote}`, header)
        .then(res => {
          console.log(res);
        })
        .catch(err => {
          console.log(err);
        })
    }
  },
  created() {
    this.lote()
    this.listarSaleLotCattle()
  }
};
</script>

