<template>
  <v-app>
    <v-container fluid>
      <v-row class="mx-1" no-gutters>
        <v-col cols="12" style="text-align: center">
          <v-row no-gutters>
            <h3 class="red--text ml-3 mb-4">Datos del lote No. {{ lote }}</h3>
            <v-spacer></v-spacer>
            <div align-items="center">
              <h3 v-if="estado == 3" class="mr-10">En Remate</h3>
              <h3 v-if="estado == 4" class="mr-10">Desierto</h3>
              <h3 v-if="estado == 2" class="mr-10">Terminado</h3>
            </div>
          </v-row>
        </v-col>
        <v-col cols="3">
          <v-row class="mx-2">
            <p
              class="font-weight-black black--text"
              style="font-size: 16px; text-align: center"
            >
              Clase de lote:
              <span
                class="font-weight-black red--text"
                style="font-size: 14px"
                >{{ clase }}</span
              >
            </p>
          </v-row>
          <v-row class="mx-2">
            <p
              class="font-weight-black black--text"
              style="font-size: 16px; text-align: center"
            >
              Raza: <span class="font-weight-black red--text" style="font-size: 14px">{{ raza }}</span>
            </p>
          </v-row>
        </v-col>
        <v-col cols="3">
          <v-row class="mx-2">
            <p
              class="font-weight-black black--text"
              style="font-size: 16px; text-align: center"
            >
              Crias hembra:
              <span class="font-weight-black red--text" style="font-size: 14px">{{ criasH }}</span>
            </p>
          </v-row>
          <v-row class="mx-2">
            <p
              class="font-weight-black black--text"
              style="font-size: 16px; text-align: center"
            >
              Crias Macho:
              <span class="font-weight-black red--text" style="font-size: 14px">{{ criasM }}</span>
            </p>
          </v-row>
        </v-col>
        <v-col cols="3">
          <v-row class="mx-2">
            <p
              class="font-weight-black black--text"
              style="font-size: 16px; text-align: center"
            >
              Peso total:
              <span class="font-weight-black red--text" style="font-size: 14px">{{ peso }}</span>
            </p>
          </v-row>
          <v-row class="mx-2">
            <p
              class="font-weight-black black--text"
              style="font-size: 16px; text-align: center"
            >
              Peso promedio:
              <span class="font-weight-black red--text" style="font-size: 14px">{{ pesoProm }}</span>
            </p>
          </v-row>
        </v-col>
        <v-col cols="3">
          <v-row class="mx-2">
            <p
              class="font-weight-black black--text"
              style="font-size: 16px; text-align: center"
            >
              Cantidad de reces:
              <span class="font-weight-black red--text" style="font-size: 14px">{{ cantidad }}</span>
            </p>
          </v-row>
          <v-row class="mx-2">
            <p
              class="font-weight-black black--text"
              style="font-size: 16px; text-align: center"
            >
              Origen:
              <span
                class="font-weight-black red--text"
                style="font-size: 14px"
                >{{ origen }}</span
              >
            </p>
          </v-row>
        </v-col>
      </v-row>
      <v-row>

        <!-- seccion de datos de la subasta -->

        <v-col cols="4">
          <v-card class="car">
            <v-card-title>
              <v-row>
                <h2 class="my-4 ml-5">Datos del Remate</h2>
                <v-row> </v-row>
                <v-spacer></v-spacer>
                <v-btn
                  @click="irTablero()"
                  outlined
                  color="red"
                  class="my-4 ml-5 mr-3"
                  >Tablero</v-btn
                >
              </v-row>
            </v-card-title>
            <v-card-text>
              <v-row no gutters>
                <v-col cols="12" md="6">
                  <v-subheader style="font-size: 22px; text-align: center"
                    >Precio Base</v-subheader
                  >
                </v-col>
                <v-col cols="12" md="6">
                  <v-text-field
                    style="font-size: 25px; text-align: center"
                    v-model="precioBase"
                    label="Precio Base"
                    value="0.0"
                    prefix="$"
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-btn
                    block
                    outlined
                    color="green"
                    @click="precioInicial()"
                    v-if="estado != 2 && estado != 4"
                    >Establecer</v-btn
                  >
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="4">
                  <v-subheader style="font-size: 22px; text-align: center"
                    >Puja</v-subheader
                  >
                </v-col>
                <v-col cols="8">
                  <v-text-field
                    style="font-size: 25px; text-align: center"
                    size="13"
                    label="Puja"
                    v-model="precioFin"
                    value="0.0"
                    prefix="$"
                    append-outer-icon="mdi-send"
                    @click:append-outer="cambiarPuja()"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-divider class="mx-4 my-4"></v-divider>
              <v-row>
                <v-col cols="5" align="center">
                  <v-img
                    src="../assets/icons/subastador2.jpeg"
                    height="100"
                    width="100"
                  ></v-img>
                </v-col>
                <v-col cols="7" align-self="center">
                  <v-text-field
                    style="font-size: 25px; text-align: center"
                    label="Numero de paleta"
                    prefix="#"
                    v-model="paleta"
                  ></v-text-field>
                </v-col>
                <v-col cols="4" align-self="center" align="center"> </v-col>
              </v-row>
              <v-row>
                <v-col cols="12">
                  <v-btn
                    block
                    v-if="estado != 2 && estado != 4 && estado != 1"
                    @click="adjudicar()"
                    color="green"
                    outlined
                  >
                    <span>Adjudicar Remate</span>
                  </v-btn>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="6">
                  <v-btn
                    v-if="estado != 2 && estado != 4 && estado != 1"
                    @click="subastaDesierta()"
                    outlined
                    color="red"
                  >
                    
                    <span>Autoremate</span>
                  </v-btn>
                </v-col>
                <v-col cols="6">
                  <v-btn
                    v-if="estado == 2 || estado == 3"
                    @click="reiniciarSubasta()"
                    outlined
                    color="orange"
                  >
                    <span>Reiniciar Remate</span>
                    
                  </v-btn>
                </v-col>
              </v-row>
            </v-card-text>
            <!--  <v-card-actions>
              <v-btn
                v-if="estado != 2 && estado != 4"
                @click="subastaDesierta()"
                outlined
                color="red"
                class="my-5 ml-5"
              >
                Subasta Desierta
              </v-btn>
              <v-spacer> </v-spacer>
              <v-btn
                v-if="estado != 2 && estado != 4"
                class="my-5 mr-5"
                @click="adjudicar()"
                color="green"
                outlined
                >Adjudicar Subasta
              </v-btn>
            </v-card-actions> -->
          </v-card>
        </v-col>

        <!-- seccion de paletera -->

        <v-col cols="6">
          <v-row class="fila">
            <v-col cols="12" class="text-center">
              <v-row class="d-flex justify-center">
                <h2>Paletera</h2>
              </v-row>
            </v-col>
            <v-col cols="12">
              <v-row>
                <v-col
                  class="mb-3 fila"
                  cols="2"
                  v-for="(p, i) in participantesFrecuentes.slice(0, 5)"
                  :key="i"
                >
                  <v-card class="text-center elevation-0">
                    <v-avatar color="pink" size="68" @click="participante(p)">
                      <h1 class="white--text">{{ p.consecutiveholder }}</h1>
                    </v-avatar>
                  </v-card>
                </v-col>
              </v-row>
            </v-col>

            <v-col cols="12" class="text-center mb-3">
              <v-btn block outlined color="orange" @click="EliminarPuja()"
                >Eliminar ultima puja</v-btn
              >
            </v-col>
            <v-col
              class="mb-3"
              cols="1"
              v-for="(p, i) in participantes"
              :key="i"
            >
              <v-card class="text-center elevation-0">
                <v-avatar color="pink" size="53" @click="participante(p)">
                  <h2 class="white--text">{{ p.consecutiveholder }}</h2>
                </v-avatar>
              </v-card>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="6" v-for="(b, i) in pujas" :key="i">
              <v-row no gutters>
                <v-col cols="12" md="2" align-self="center">
                  <h4 style="text-align: center">Item {{ i + 1 }}</h4>
                </v-col>
                <v-col cols="12" md="4">
                  <v-text-field
                    style="font-size: 25px; text-align: center"
                    label="Numero de paleta"
                    prefix="#"
                    disabled
                    :value="b.consecutiveholder"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="6">
                  <v-text-field
                    style="font-size: 25px; text-align: center"
                    :value="b.totalprice"
                    label="Puja"
                    disabled
                    prefix="$"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-col>
        <v-col cols="2">
          <v-row class="d-flex justify-center mb-1" no-gutters>
            <h2>Incremento</h2>
          </v-row>
          <v-col cols="12" class="text-center">
            <v-btn-toggle
              dense
              background-color="white"
              v-model="toggle_exclusive"
              mandatory
              style="flex-direction: column; justify-content: center"
            >
              <v-btn
                block
                color="blue"
                outlined
                class="font-weight-black white--text mb-6"
                style="font-size: 18px"
                @click="IntervaloPrecio(100000)"
                >100.000</v-btn
              >
              <v-btn
                block
                color="blue"
                outlined
                class="font-weight-black white--text mb-6"
                style="font-size: 18px"
                @click="IntervaloPrecio(200000)"
                >200.000</v-btn
              >
              <v-btn
                block
                color="blue"
                outlined
                class="font-weight-black white--text mb-6"
                style="font-size: 18px"
                @click="IntervaloPrecio(500000)"
                >500.000</v-btn
              >
              <v-btn
                block
                color="primary"
                outlined
                class="font-weight-black white--text mb-6"
                style="font-size: 18px"
                @click="IntervaloPrecio(1000000)"
                >1.000.000</v-btn
              >
            </v-btn-toggle>
          </v-col>
        </v-col>

        <!-- <v-row>
            <v-col cols="12" class="text-center">
              <v-row>
                <v-col cols="6">
                  <h2>Precios</h2>
                </v-col>
                <v-col cols="6">
                  <v-spacer></v-spacer>
                  <v-btn outlined color="green" @click="enviarPuja()" v-if="estado != 2 && estado != 4">Enviar puja</v-btn>
                </v-col>
              </v-row>
            </v-col>
            <v-col cols="4" v-for="(p, i) in preciosBotonera" :key="i">
              <v-row>
                <v-col cols="12">
                  <v-card class="text-center elevation-0">
                    <v-avatar rounded color="blue" size="68" @click="botonera(p)">
                      <h3 class="white--text">{{ parseInt(precioKg) + parseInt(p) }}</h3>
                    </v-avatar>
                  </v-card>
                </v-col>
              </v-row>
            </v-col>
            <v-row class="mx-5">
                <v-col cols="4" align-self="center">
                  <h3>Intervalo</h3>
                </v-col>
                <v-col cols="8">
                  <v-text-field v-model="intervalo" placeholder="Seleccione el intervalo de precios">
                  </v-text-field>
                </v-col>
              </v-row>
          </v-row> -->
      </v-row>
    </v-container>
  </v-app>
</template>

<script>
//import axios from "axios";
import Swal from "sweetalert2";
import {url} from "../main.js"
export default {
  name: "SaleLotCattle",
  data() {
    return {
      precioFin: 0,
      precioTemp:0,
      tipo: localStorage.getItem("tipo"),
      toggle_exclusive: 0,
      participantes: [],
      participantesFrecuentes: [],
      subasta: "",
      id: "",
      idSubasta: "",
      idSaleHolder: "",
      lote: "",
      estado: 1,
      peso: 0,
      pesoProm: 0,
      origen: "",
      cantidad: 0,
      clase: "",
      raza: "",
      criasM: 0,
      criasH: 0,
      precioInicia: 0,
      precioFinal: 0,
      precioTotal: 0,
      holderActual: "",
      paleta: "",
      precioBase: 0,
      incremento: 100000,
      pujas: [],
      preciosBotonera: [],
      intervalos: [50, 100, 150, 200, 250, 300, 400, 500, 1000],
      intervalo: 0,
    };
  },

  sockets: {
    connect() {
      this.isConnected = true;
      console.log("conectado");
      this.$socket.emit("pidiendoinfoinicial", (subasta) => {
        if (!subasta) return;
        console.log(subasta);
        this.idSubasta = subasta;
        console.log(this.idSubasta);
        this.id = subasta._id;
        this.estado = subasta.state;
        console.log(this.estado);
        this.lote = subasta.lot;
        this.peso = subasta.weight;
        console.log(this.peso);
        this.pesoProm = subasta.weightavg;
        this.participantes = subasta.holders.sort((a, b) => a.consecutiveholder - b.consecutiveholder);
        console.log(this.participantes);
        this.cantidad = subasta.quantity;
        console.log(this.cantidad);
        this.clase = subasta.classcattle;
        console.log(this.clase);
        this.origen = subasta.origin;
        console.log(this.origen);
        this.raza = subasta.breed.description;
        console.log(this.raza);
        this.criasH = subasta.calffemale;
        this.criasM = subasta.calfmale;
        this.precioInicia = subasta.initialprice;
        this.precioFinal = subasta.pricekg;
        console.log(this.precioFinal);
        this.precioTotal = subasta.price;
        console.log(this.precioTotal);
        this.precioFin = subasta.pricetoget;
        this.precioBase = subasta.initialprice;
        this.precioTemp=subasta.totalpricetemp
        //this.holderActual = subasta.currentholder;
        this.paleta = subasta.consecutiveholder;
        console.log(this.paleta);
        /*   this.participantesFrecuentes = subasta.bids.filter(function(item, pos) {
          return subasta.bids.indexOf(item.consecutiveholder) == pos;
        }) */
        //this.participantesFrecuentes =  [...new Set(subasta.bids.consecutiveholder)];
        this.participantesFrecuentes = subasta.bids.filter(
          (value, index, self) =>
            index ===
            self.findIndex(
              (t) => t.consecutiveholder === value.consecutiveholder
            )
        );

        console.log(this.participantesFrecuentes);
        this.pujas = subasta.bids.reverse();
        console.log(subasta);
      });
    },
    disconnect() {
      this.isConnected = false;
      console.log("desconectado");
    },
  },
  methods: {
    participante(p) {
      console.log(p);
      this.paleta = p.consecutiveholder;
      this.idSaleHolder = p.holder._id;

      const idLotCattle = this.id;
      const total = this.precioFin;
      const holderActual = this.idSaleHolder;
      const paleta = this.paleta;
      const preciokg = this.precioFin / this.peso;
      const cantidad = this.cantidad;
      const incremento = this.incremento;

      console.log("total " + parseInt(this.precioFin) * parseInt(this.peso));

      const subasta = {
        idLotCattle,
        total,
        holderActual,
        paleta,
        preciokg,
        cantidad,
        incremento,
      };
      console.log(subasta);
      this.$socket.emit("guarderemate", subasta, (msg) => {
        console.log(msg);
        this.precioFin = msg.pricetoget;
        this.pujas = msg.bids.reverse();
        this.precioTemp=msg.totalpricetemp
        this.participantesFrecuentes = msg.bids.filter(
          (value, index, self) =>
            index ===
            self.findIndex(
              (t) => t.consecutiveholder === value.consecutiveholder
            )
        );
      });
    },

    botonera(b) {
      this.precioFin = parseInt(this.precioKg) + parseInt(b);
      console.log(this.precioFin);
    },

    adjudicar() {
      Swal.fire({
        title: "Cambiar de estado",
        text: "Esta seguro que desea terminar la subasta?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "green",
        cancelButtonColor: "red",
        confirmButtonText: "Si, cambiar estado",
      }).then((result) => {
        if (result.isConfirmed) {
          const idLotCattle = this.id;
          const subasta = {
            idLotCattle,
          };
          this.$socket.emit("adjudiquesubasta", subasta, (msg) => {
            console.log(msg);
            console.log(msg.state);
            this.estado = msg.state;
          });
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Subasta terminada",
            showConfirmButton: false,
            timer: 2000,
          });
        }
      });
    },
    precioInicial() {
      console.log(this.idSubasta);
      console.log(this.precioBase);
      const idLotCattle = this.id;
      const precioInicial = this.precioBase;
      const subasta = {
        idLotCattle,
        precioInicial,
      };

      console.log(subasta);
      this.$socket.emit("asigneprecioinicial", subasta, (msg) => {
        console.log(msg);
        this.precioFin = msg.pricetoget;
      });
    },

    subastaDesierta() {
      Swal.fire({
        title: "Cambiar de estado",
        text: "Esta seguro que desea terminar la subasta?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "green",
        cancelButtonColor: "red",
        confirmButtonText: "Si, cambiar estado",
      }).then((result) => {
        if (result.isConfirmed) {
          const idLotCattle = this.id;
          const subasta = {
            idLotCattle,
          };
          this.$socket.emit("declaresubastadesierta", subasta, (msg) => {
            console.log(msg);
            this.estado = msg.state;
          });
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Subasta Desierta",
            showConfirmButton: false,
            timer: 2000,
          });
        }
      });
    },

    /*  enviarRemate() {
      const idLotCattle = this.id;
      console.log(this.subasta);

      const precioActual = this.precioFin;
      const holderActual = this.idSaleHolder;
      const paleta = this.paleta;
      const total = this.precioFin * this.peso;
      console.log("total " + parseInt(this.precioFin) * parseInt(this.peso));

      const subasta = {
        idLotCattle,
        precioActual,
        holderActual,
        paleta,
        total,
      };
      console.log(subasta);
      this.$socket.emit("guarderemate", subasta, (msg) => {
        console.log(msg);
      });
    }, */
    EliminarPuja() {
      let idLotCattle = this.id;
      const subasta = {
        idLotCattle,
      };
      this.$socket.emit("elimineultima", subasta, (msg) => {
        this.pujas = msg.bids.reverse();
      });
    },

    reiniciarSubasta() {
      console.log(this.id);
      let idLotCattle = this.id;
      const subasta = {
        idLotCattle,
      };
      Swal.fire({
        title: "Esta seguro que desea terminar el remate?",
        text: "Se perderan todos los cambios",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "green",
        cancelButtonColor: "red",
        confirmButtonText: "Si, cambiar estado",
      }).then((result) => {
        if (result.isConfirmed) {
          this.$socket.emit("reiniciarsubasta", subasta, (msg) => {
            console.log(msg.lotCattle.state);
            this.estado = msg.lotCattle.state;
            window.close();
          });
        }
      });
    },
    irTablero() {
      window.open(`${url}tablero`)
    },


    cambiarPuja(){
      let idLotCattle = this.id;
      const precioEsperado=this.precioFin
      console.log(precioEsperado+ "  "+idLotCattle);
      const subasta = {
        idLotCattle,
        precioEsperado
      };

      this.$socket.emit("actualiceprecioesperado",(subasta),msg=>{
        console.log(msg);
      })
    },

    IntervaloPrecio(valor){
      this.incremento=valor
      this.precioFin=this.precioTemp+valor
    }
  },
  computed: {
   
  },

  created(){
    
  }
};
</script>
<style scoped>
.car {
  border: 1px solid green !important;
}

.btn-toggle {
  flex-direction: column !important;
}
.car2 {
  border-left: 1px solid red !important;
  border-right: 1px solid red !important;
  border-bottom: 1px solid red !important;
}

.columna {
  border-left: 1px solid green !important;
}

.fila {
  border-bottom: 1px solid red !important;
}
</style>